/* Dashboard.css */
body {
  background-color: #f0f8ff; /* Light blue background */
  margin: 0;
  font-family: Arial, sans-serif;
}

.dashboard-container {
  padding: 20px;
  text-align: center;
}

.dashboard-container h1 {
  color: #007bff; /* Blue welcome text */
  margin-bottom: 10px;
}

.dashboard-container h2 {
  color: #333; /* Dark gray section heading */
  margin-top: 20px;
}

.table-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.market-table {
  border-collapse: collapse;
  width: 80%;
  max-width: 600px;
}

.market-table th,
.market-table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: left;
}

.market-table th {
  background-color: #007bff;
  color: #fff;
}

.market-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.market-table tr:hover {
  background-color: #f1f1f1;
}

.logout-button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #dc3545; /* Red button */
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

.logout-button:hover {
  background-color: #c82333; /* Darker red on hover */
}
