/* Register.css */
body {
  background-color: #f9f9fb; /* Light gray background */
  margin: 0;
  font-family: Arial, sans-serif;
}

.register-container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.register-card {
  width: 400px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #ffffff; /* White card */
}

.register-card h2 {
  text-align: center;
  color: #2c3e50; /* Dark header color */
  margin-bottom: 20px;
}

.register-card label {
  display: block;
  margin-bottom: 8px;
  color: #555; /* Gray label text */
}

.register-card input {
  width: 100%;
  padding: 10px;
  margin-bottom: 16px;
  border-radius: 5px;
  border: 1px solid #ddd;
  box-sizing: border-box;
}

.register-card button {
  width: 100%;
  padding: 10px;
  background-color: #4caf50; /* Green button */
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.register-card button:hover {
  background-color: #388e3c; /* Darker green on hover */
}

.register-card p {
  text-align: center;
  margin-top: 16px;
  color: #666; /* Light gray text */
}

.register-card a {
  color: #4caf50; /* Green links */
  text-decoration: none;
}
