/* Login.css */
body {
  background-color: #f0f4f8; /* Light background color */
  margin: 0;
  font-family: Arial, sans-serif;
}

.login-container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-card {
  width: 400px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #ffffff; /* White card */
}

.login-card h2 {
  text-align: center;
  color: #007bff; /* Blue header */
  margin-bottom: 20px;
}

.login-card label {
  display: block;
  margin-bottom: 8px;
  color: #333; /* Dark gray label text */
}

.login-card input {
  width: 100%;
  padding: 10px;
  margin-bottom: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.login-card button {
  width: 100%;
  padding: 10px;
  background-color: #007bff; /* Blue button */
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

.login-card button:hover {
  background-color: #0056b3; /* Darker blue on hover */
}

.login-card p {
  text-align: center;
  margin-top: 16px;
  color: #555; /* Light gray text */
}

.login-card a {
  color: #007bff; /* Blue links */
  text-decoration: none;
}
